



























import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import wx from 'weixin-js-sdk'

@Component({
  components: {
    HelloWorld
  }
})
export default class Home extends Vue {
  $createDialog : any
  $createToast : any
  // 声明周期钩子
  model = { name: '', password: '' }
  fields =[{ type: 'input', modelKey: 'name', label: '手机号', props: { placeholder: '请输入' }, rules: { required: true } }, { type: 'input', modelKey: 'verificationCode', label: '验证码', props: { placeholder: '请输入', type: 'text' }, rules: { required: true } }]
  valid = undefined
  validity = {}
  options = {}
  time = 60 // 验证码等待时间
  sendMsgDisabled = false
  isVerification = false // 是否需要开启验证码
  mounted () {
    console.log('钩子')
  }

  @Prop({ default: 'default value' }) readonly propB!: string
  @Watch('$route')
  onRouterChanged () {
    console.log('路由编号')
  }

  // 计算属性
  get computedMsg () {
    return 'computed ' + this.propB
  }
  showAlert () {
    this['$createDialog']({
      type: 'alert',
      title: '我是标题',
      content: '我是内容',
      icon: 'cubeic-alert'
    }).show()
  }
  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  submitHandler (e : any) {
    e.preventDefault()
    console.log(this.model)
    const toast = this['$createToast']({
      time: 0,
      txt: '绑定中...'
    })
    toast.show()
    // this.$store.dispatch('login', { user: user, pass: pass }).then((response) => {
    //   toast.hide()
    //   this.loginClick = true
    //   if (!response.success) {
    //     this['$createDialog']({
    //       type: 'alert',
    //       content: '账号或密码错误',
    //       icon: 'cubeic-wrong'
    //     }).show()
    //   }
    // })
    this.isVerification = true
    this.send()
  }
  submitClick () {
    return false
  }

  send() {
    let me = this
    if (me.sendMsgDisabled) {
      return false
    }
    me.sendMsgDisabled = true
    let interval = window.setInterval(function () {
      if ((me.time--) <= 0) {
        me.time = 10
        me.sendMsgDisabled = false
        window.clearInterval(interval)
      }
    }, 1000)
  }
}
